import React from 'react'
import { Link } from 'gatsby'

import Layout from "../components/layout"
import SEO from "../components/seo"

// import NoWidows from '../utils/NoWidows';

export default ({ data }) => {
  const seomatic = data.craft.seomatic;
  let disciplines = [];

  data.craft.entries.forEach(({ disciplines }) => {
    disciplines.forEach(({ title }) => {
      let checkDiscipline = title.toLowerCase();

      if(disciplines.indexOf(checkDiscipline) === -1){
        disciplines.push(checkDiscipline);
      }
    })
  })

  // disciplines is set here to prevent accidental additions in CMS
  disciplines = [
    "brand & identity",
    "film & photography",
    "spatial",
    "digital"
  ];

  return (
    <Layout className="about" parentClass="-dark-theme">
      <SEO data={ seomatic }/>
      <div className="row about__content">
        <div className="col__7 col__s-12">
          <h2 className="about__text">We are for design working for better. We believe in the positive difference design can make.</h2>
          <h2 className="about__text">Adding real value to our New Zealand stories on the biggest stage. Working as close advisers and partners to ambitious clients.</h2>
          <h2 className="about__text">As the most truly multidisciplinary practice in the southern hemisphere.</h2>
          <h3 className="about__text">From physical to digital, spatial to motion, product to identity, our integrated teams are able to bring it all together for better creative and commercial outcomes.</h3>
          <Link to="/contact">
            <p className="about__contactLink fontSize-h3">Get in touch</p>
          </Link>
        </div>
        <div className="col__2 col__s-0"></div>
        <div className="col__3 col__s-12">
          <div className="about__offerings">
            <h3 className="about__offerings-title fontSize-h3">Our offerings</h3>

            {disciplines.map((discipline) => {
              return(
                <Link key={ discipline } to={ "/work/" + discipline }>
                  <p className="about__offerings-discipline fontSize-h3">{ discipline }</p>
                </Link>
              )
            })}
          </div>
          <div className="about__offerings">
              <h3 className="about__offerings-title fontSize-h3">Our products</h3>
              <a target="_blank" href="http://www.whstl.com/">
                <p className="about__offerings-discipline fontSize-h3">Whstl</p>
              </a>
          </div>
        </div>
      </div>
    </Layout>
  ) 
}

export const query = graphql`
  query {
    craft {
      seomatic(uri: "/about") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }

      entries(section: [projects]) {
        ... on Craft_Projects {
          disciplines {
            title
          }
        }
      }
    }
  }  
`;